import { useState } from "react";
import { Link } from "react-router-dom";
import { Menu, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import LogoIcon from "@/assets/LogoIcon";

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => setMenuOpen(!menuOpen);
  const closeMenu = () => setMenuOpen(false);

  return (
    <header className="border-b border-gray-800 sticky top-0 z-50 bg-background bg-opacity-95 backdrop-blur-sm">
      <div className="container mx-auto px-4 py-3 flex justify-between items-center">
        <Link to="/" className="flex items-center" onClick={closeMenu}>
          <span className="text-white text-xl font-bold">
            mod<span className="text-primary">haven</span>
          </span>
          <LogoIcon />
        </Link>
        
        <nav className="hidden md:flex space-x-6">
          <Link to="/" className="text-white hover:text-primary transition">
            Home
          </Link>
          <a href="#games" className="text-white hover:text-primary transition">
            Games
          </a>
          <a href="#features" className="text-white hover:text-primary transition">
            Features
          </a>
          <a href="#support" className="text-white hover:text-primary transition">
            Support
          </a>
        </nav>
        
        <Button 
          variant="ghost" 
          size="icon" 
          className="md:hidden text-white focus:outline-none"
          onClick={toggleMenu}
        >
          {menuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
        </Button>
      </div>
      
      {/* Mobile menu */}
      {menuOpen && (
        <div className="md:hidden bg-background border-b border-gray-800">
          <div className="container mx-auto px-4 py-4 flex flex-col space-y-4">
            <Link 
              to="/" 
              className="text-white hover:text-primary transition"
              onClick={closeMenu}
            >
              Home
            </Link>
            <a 
              href="#games" 
              className="text-white hover:text-primary transition"
              onClick={closeMenu}
            >
              Games
            </a>
            <a 
              href="#features" 
              className="text-white hover:text-primary transition"
              onClick={closeMenu}
            >
              Features
            </a>
            <a 
              href="#support" 
              className="text-white hover:text-primary transition"
              onClick={closeMenu}
            >
              Support
            </a>
          </div>
        </div>
      )}
    </header>
  );
}
