import { useLocation } from "wouter";
import { useEffect } from "react";

export interface SeoProps {
  title?: string;
  description?: string;
  keywords?: string;
  ogImage?: string;
  ogType?: "website" | "article";
  twitterCard?: "summary" | "summary_large_image";
}

export default function Seo({
  title = "ModHaven - Your Ultimate Mod Menu",
  description = "Enhance your gaming experience with free, high-quality mod menus for your favorite games. Easy to install game mods with no ads.",
  keywords = "game mods, mod menu, game cheats, gaming tools, free mods, game hacks, gaming enhancements",
  ogImage = "/og-image.svg",
  ogType = "website",
  twitterCard = "summary_large_image"
}: SeoProps) {
  const [location] = useLocation();
  const siteUrl = import.meta.env.PROD ? "https://yourdomain.github.io/modhaven" : "http://localhost:5000";
  const canonicalUrl = `${siteUrl}${location}`;
  
  // Динамически обновляем мета-теги
  useEffect(() => {
    // Обновляем заголовок
    document.title = title;
    
    // Обновляем или создаем мета-теги
    const metaTags: Record<string, string> = {
      "description": description,
      "keywords": keywords,
      "og:title": title,
      "og:description": description,
      "og:image": ogImage.startsWith("http") ? ogImage : `${siteUrl}${ogImage}`,
      "og:url": canonicalUrl,
      "og:type": ogType,
      "twitter:card": twitterCard,
      "twitter:title": title,
      "twitter:description": description,
      "twitter:image": ogImage.startsWith("http") ? ogImage : `${siteUrl}${ogImage}`
    };
    
    Object.entries(metaTags).forEach(([name, content]) => {
      // Проверяем, существует ли уже мета-тег
      let metaTag = document.querySelector(`meta[property="${name}"]`) || 
                    document.querySelector(`meta[name="${name}"]`);
      
      if (!metaTag) {
        // Создаем новый мета-тег, если он не существует
        metaTag = document.createElement('meta');
        if (name.startsWith('og:') || name.startsWith('twitter:')) {
          metaTag.setAttribute('property', name);
        } else {
          metaTag.setAttribute('name', name);
        }
        document.head.appendChild(metaTag);
      }
      
      // Устанавливаем или обновляем содержимое
      metaTag.setAttribute('content', content);
    });
    
    // Обновляем канонический URL
    let canonicalLink = document.querySelector('link[rel="canonical"]');
    if (!canonicalLink) {
      canonicalLink = document.createElement('link');
      canonicalLink.setAttribute('rel', 'canonical');
      document.head.appendChild(canonicalLink);
    }
    canonicalLink.setAttribute('href', canonicalUrl);
    
    // Добавляем иконку
    let favicon = document.querySelector('link[rel="icon"]');
    if (!favicon) {
      favicon = document.createElement('link');
      favicon.setAttribute('rel', 'icon');
      document.head.appendChild(favicon);
    }
    favicon.setAttribute('href', '/favicon.svg');
    favicon.setAttribute('type', 'image/svg+xml');
    
  }, [title, description, keywords, ogImage, ogType, twitterCard, canonicalUrl, siteUrl]);
  
  return null; // Этот компонент не рендерит видимый контент
}