import { useState } from "react";
import { ToggleSwitch } from "@/components/ui/toggle-switch";

export interface ModOption {
  id: string;
  name: string;
  enabled: boolean;
}

interface ModMenuProps {
  version: string;
  modOptions: ModOption[];
  onOptionChange?: (id: string, enabled: boolean) => void;
}

export default function ModMenu({ version, modOptions, onOptionChange }: ModMenuProps) {
  const [options, setOptions] = useState<ModOption[]>(modOptions);

  const handleToggle = (id: string, enabled: boolean) => {
    const updatedOptions = options.map(option => 
      option.id === id ? { ...option, enabled } : option
    );
    
    setOptions(updatedOptions);
    
    if (onOptionChange) {
      onOptionChange(id, enabled);
    }
  };

  return (
    <div className="w-full max-w-sm bg-card border border-gray-700 rounded-lg p-4 relative">
      <div className="flex justify-between items-center mb-4">
        <span className="text-xs text-muted-foreground">{version}</span>
        <div className="flex items-center space-x-1">
          <span className="bg-secondary text-xs px-2 py-0.5 rounded-full">Menu Options</span>
        </div>
      </div>
      
      {/* Toggle Options */}
      <div className="space-y-3">
        {options.map((option) => (
          <ToggleSwitch
            key={option.id}
            label={option.name}
            checked={option.enabled}
            onCheckedChange={(checked) => handleToggle(option.id, checked)}
          />
        ))}
      </div>
    </div>
  );
}
