import { useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import { Button } from "@/components/ui/button";
import ModMenu from "@/components/shared/ModMenu";
import GameCard from "@/components/shared/GameCard";
import FeatureCard from "@/components/shared/FeatureCard";
import Seo from "@/components/shared/Seo";
import { getGameById, getOtherGames } from "@/data/games";
import { features, installSteps } from "@/data/features";

export default function GameDetail() {
  const { id } = useParams<{ id: string }>();
  const game = getGameById(id || "");
  const otherGames = getOtherGames(id || "");

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [id]);

  if (!game) {
    return (
      <div className="container mx-auto px-4 py-20">
        <Seo 
          title="Game Not Found - ModHaven"
          description="The game you are looking for could not be found."
        />
        <h1 className="text-3xl font-bold mb-4">Game Not Found</h1>
        <p className="text-gray-400 mb-8">
          The game you are looking for could not be found.
        </p>
        <Link to="/" className="text-primary hover:text-primary/80 transition">
          ← Back to Home
        </Link>
      </div>
    );
  }

  return (
    <div>
      <Seo 
        title={`${game.title} Mod Menu - ModHaven`}
        description={`Ultimate ${game.title} Mod Menu: ${game.description}. Easy to install game mod with no ads.`}
        keywords={`${game.title} mod, ${game.title} hack, ${game.title} cheat, game mods, mod menu`}
        ogImage={game.imageSrc}
      />
      {/* Game Hero Section */}
      <section className="py-16 relative overflow-hidden">
        <div className="absolute inset-0 z-0 opacity-20">
          <img 
            src={game.imageSrc} 
            alt="" 
            className="w-full h-full object-cover" 
          />
        </div>
        <div className="container mx-auto px-4 relative z-10">
          <div className="flex flex-col md:flex-row items-center">
            <div className="md:w-1/2 mb-10 md:mb-0">
              <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold mb-6">
                Ultimate {game.title} <span className="text-primary">Mod Menu</span>
              </h1>
              <p className="text-lg text-gray-300 mb-8 max-w-lg">
                {game.fullDescription}
              </p>
              <div className="flex flex-col sm:flex-row space-y-4 sm:space-y-0 sm:space-x-4">
                <Button className="button-primary">
                  DOWNLOAD NOW
                </Button>
                <Button variant="outline" className="button-secondary">
                  EXPLORE FEATURES
                </Button>
              </div>
              <p className="text-sm text-gray-400 mt-4">
                Downloaded by <span className="font-semibold">{game.downloadCount} players</span>
              </p>
            </div>
            <div className="md:w-1/2 flex justify-center">
              <ModMenu 
                version={game.version}
                modOptions={game.modOptions}
              />
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section id="features" className="py-16 bg-card">
        <div className="container mx-auto px-4">
          <h2 className="section-title">
            Powerful <span className="text-primary">Features</span>
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {features.map((feature, index) => (
              <FeatureCard
                key={index}
                icon={feature.icon}
                title={feature.title}
                description={feature.description}
              />
            ))}
          </div>
        </div>
      </section>

      {/* Installation Section */}
      <section className="py-16">
        <div className="container mx-auto px-4">
          <h2 className="section-title">
            How to <span className="text-primary">Install</span>
          </h2>
          
          <div className="max-w-3xl mx-auto">
            <ol className="relative border-l border-gray-700">
              {installSteps.map((step) => (
                <li key={step.step} className="mb-10 ml-6">
                  <span className="absolute flex items-center justify-center w-8 h-8 rounded-full -left-4 bg-primary text-white">
                    {step.step}
                  </span>
                  <h3 className="font-semibold text-xl mb-2">{step.title}</h3>
                  <p className="text-gray-400 mb-4">
                    {step.description}
                  </p>
                </li>
              ))}
            </ol>
          </div>
        </div>
      </section>

      {/* Other Games Section */}
      <section className="py-16 bg-card">
        <div className="container mx-auto px-4">
          <h2 className="section-title">
            Other <span className="text-primary">Games</span>
          </h2>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {otherGames.slice(0, 4).map((game) => (
              <GameCard
                key={game.id}
                id={game.id}
                title={game.title}
                description={game.description}
                imageSrc={game.imageSrc}
              />
            ))}
          </div>
        </div>
      </section>
      
      <div className="text-center py-8">
        <Link to="/" className="text-primary hover:text-primary/80 transition">
          ← Back to Home
        </Link>
      </div>
    </div>
  );
}
